window.$ = jQuery;

$('.sidebar-items li').on('click', function() {
    $('.sidebar-items li').removeClass('active');
    $(this).addClass('active');
});

document.querySelectorAll(".datepicker").flatpickr({
    allowInput: true,
    wrap: true,
    altInput: true,
    altFormat: "M j, Y",
    dateFormat: "Y-m-d",  
});
